body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-style ul {
  display: flex;
  list-style: none;
  justify-content: center;
}

.nav-style li {
  margin-right: 20px;
  color: white;
}

.nav-style a {
  color: white;
  text-decoration: underline !important;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: red;

}

.App-link {
  color: #61dafb;
}

a {
  color: white;
  cursor: pointer;
}



.nav-style .header {
	display: flex;
	justify-content: center;
	align-items: center;
	width:100%;
}
.nav-style{
	width: 100%;
	height: 20vh;
}
.nav-bar{
	width:100%;
	height: 100px;
	background-color: red;
	display: flex;
	justify-content: flex-end;
	align-content: center;
	color: black;
}
.nav-bar-logingButtons{
	width:100%;
	height: 100px;
	background-color: red;
	display: flex;
	justify-content: flex-end;
	align-content: center;
	color: black;
}
.main{
  width: 100%;
  height: 80vh;
  background-color: rgb(41, 114, 231,0.6);
  display: flex;
  justify-content: space-between;
}
.carrousel-teams{
  width: 20%;
  height: 100%;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}
.carrousel-teams::-webkit-scrollbar { 
  display: none; 
}
.carrousel-teams-item{
  width: 100%;
  border-radius: 50%;
}
.img-team {
  width: 100%;
}
.carrousel-players{
  width:100%;
  display: flex;
  justify-content: center;
  align-content: center;
}
.carrousel-players-wrapper{
  width: 80%;
  height: 80%;
  background-color: black;
  align-self: center;
}

.Usercard-container{
  border: 1px solid green;
  width: 100px;
  
}
canvas{
  width: 600px !important;
  height: 600px !important;
  background-color: rgba(0, 0, 0, 0.219) ;
  
}

.team-container{
  background-color:rgba(60, 0, 255, 0.3)
}
